<script>
import user from '../services/user';
import BarChart from './BarChart.vue';

export default {
  components: {
    BarChart,
  },
  data() {
    return {
      loaded: false,
      fitbit: 'https://www.fitbit.com/oauth2/authorize?response_type=token&client_id=22C643&redirect_uri=http%3A%2F%2Flocalhost%3A8080%2Fhealth%2Fsleep&scope=activity%20heartrate%20location%20nutrition%20profile%20settings%20sleep%20social%20weight&expires_in=604800',
      device: {},
      friends: null,
      statistics: {},
      statVal: 'today',
      statOptions: [
        { text: 'Today', value: 'today' },
        { text: 'Current week', value: 'currentWeek' },
        { text: 'Previous week', value: 'previousWeek' },
        { text: 'Monthly', value: 'monthly' },
        { text: 'Overall', value: 'overall' },
      ],
      charts: ['events', 'workouts', 'exercises', 'waterLogs', 'weightLogs'],
      bmi: {},
      bmiInfo: {
        obesity: {
          color: 'red',
          text: 'Obesity weight range, when your BMI is 30.0 or higher',
        },
        overweight: {
          color: 'orange',
          text: 'Overweight weight range, when your BMI is between 25.0 and <30',
        },
        healthy: {
          color: 'green',
          text: 'Healthy weight range, when your BMI is between 18.5 and <25',
        },
        underweight: {
          color: 'blue',
          text: 'Underweight weight range, when your BMI is less than 18.5',
        },
      },
      idx: 0,
      panels: [1, 1],
    };
  },
  created() {
    this.loadStatistics();
  },
  methods: {
    async loadStatistics() {
      try {
        const { data } = await user.getStatistics();
        if (data.devices) {
          [this.device] = data.devices;
        }
        if (data.friends) {
          this.friends = data.friends;
        }
        this.statistics = {
          events: this.getDataSets(data.events, '#ffa384', 'Events'),
          workouts: this.getDataSets(data.workouts, '#b4f8c8', 'Workouts'),
          exercises: this.getDataSets(data.exercises, '#eeede7', 'Exercises'),
          waterLogs: this.getDataSets(data.waterLogs, '#2e8bc0', 'Water logs'),
          weightLogs: this.getDataSets(data.weightLogs, '#fbe7c6', 'Weight logs'),
        };
        // bmi
        this.bmi = data.bmi;
        this.loaded = true;
      } catch (error) {
        console.error(error);
      }
    },
    getDataSet(arr, backgroundColor, label) {
      return {
        labels: arr.map((e) => e[0]),
        datasets: [
          {
            label,
            backgroundColor,
            data: arr.map((e) => e[1]),
          },
        ],
      };
    },
    getDataSets(data, color, objectName) {
      return {
        monthly: this.getDataSet(data.monthly, color, `${objectName} created every month`),
        today: this.getDataSet([['Today', data.today]], color, `${objectName} created today`),
        overall: this.getDataSet([['Overall', data.overall]], color, `${objectName} created overall`),
        currentWeek: this.getDataSet(data.currentWeek, color, `${objectName} created this week`),
        previousWeek: this.getDataSet(data.previousWeek, color, `${objectName} created previous week`),
      };
    },
    setVal(val) {
      this.statVal = val;
      this.idx += 1;
    },
  },
};
</script>
